import React, {useContext, useEffect, useState} from 'react';
import {makeStyles, Theme} from '@material-ui/core/styles';
import Navigation from "../../components/Navigation";
import {Grid} from "@material-ui/core";
import TabSideNavApplicationForm from "../../components/application-form/TabSideNavApplicationForm";
import {UserStoreContext} from "../../contexts";
import TabPanelSideNavApplicationForm from "../../components/application-form/TabPanelSideNavApplicationForm";
import {UserStore} from "../../stores";
import {UserTypes} from "../../enums";

export default function ApplicationForm() {
    const classes = useStyles();
    const userContext = useContext(UserStoreContext);
    const [userStore, setUserStore] = useState<UserStore | null>();
    const [value, setValue] = React.useState(0);
    const [isUser, setIsUser] = useState<boolean>(true);
    const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        setValue(newValue);
    };

    useEffect(() => {
        if (userContext){
            setUserStore(userContext);
            if (userContext.userData.role === UserTypes.ADMIN) {
                setValue(1);
                setIsUser(false);
            }
        }
    }, [userContext]);

    return (<div className={classes.root}>
        <Navigation/>
        <main className={classes.content}>
            <div className={classes.toolbar}/>
            <Grid container>
                <Grid item xs={4} sm={2}>
                    <TabSideNavApplicationForm
                        value={value}
                        userStore={userStore}
                        onChange={handleChange}
                        showConfirmation={isUser}
                    />
                </Grid>
                <Grid item xs={8} sm={10}>
                    <TabPanelSideNavApplicationForm
                        value={value}
                        userStore={userStore}
                    />
                </Grid>
            </Grid>
        </main>
    </div>);
}

const useStyles = makeStyles((theme: Theme) => ({
    root: {
        display: 'flex',
    },
    toolbar: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        padding: theme.spacing(0, 1),
        ...theme.mixins.toolbar,
    },
    content: {
        flexGrow: 1,
        paddingTop: theme.spacing(3),
        paddingRight: theme.spacing(3),
        paddingBottom: theme.spacing(3),
    },
}));