import {UserStore} from "../../stores";
import React from "react";
import {Divider, Grid} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import {PersonalDetails} from "./PersonalDetails";
import {EducationBackground} from "./EducationBackground";
import {HealthCondition} from "./HealthCondition";
import {PersonalStatement} from "./PersonalStatement";
import {EmergencyContactDetail} from "./EmergencyContact";
import {ApplicantMedia} from "./ApplicantMedia";
import {ApplicationFormConfirm} from "./ApplicationFormConfirm";
import Box from "@material-ui/core/Box";
import {useObserver} from "mobx-react";

interface TabPanelProps {
    children?: React.ReactNode;
    index: any;
    value: any;
}

function TabPanel(props: TabPanelProps) {
    const {children, value, index, ...other} = props;

    return (
        <Typography
            component="div"
            role="tabpanel"
            hidden={value !== index}
            id={`vertical-tabpanel-${index}`}
            aria-labelledby={`vertical-tab-${index}`}
            {...other}
        >
            {value === index && <Box p={3}>{children}</Box>}
        </Typography>
    );
}

interface ITabPanelSideNavApplicationForm {
    value: any,
    userStore?: UserStore | null;
}

const TabPanelSideNavApplicationForm: React.FC<ITabPanelSideNavApplicationForm> = ({
                                                                                       value,
                                                                                       userStore,
                                                                                   }) => {

    return useObserver(() => (<>
        <TabPanel value={value} index={0}>
            <Grid item>
                <Typography variant="h3" gutterBottom>
                    Application Form
                </Typography>
                <Divider/>
                <Typography variant="body1" gutterBottom>
                    Before starting your application, please prepare the following documents:
                </Typography>
                <ul>
                    <li>Short essay about your motivation in applying for UM iCamp 2020 what knowledge
                        you
                        seek to gain,
                        and how UM iCamp program will benefit from your attendance. (max. 500 words)
                    </li>
                    <li>Short essay about your activities or experiences in your community. (max. 500
                        words)
                    </li>
                    <li>Short argument in aspects that make you the most suitable candidate for the
                        program.
                    </li>
                    <li>A Formal Photo in Colour (passport size photo 4 cm x 6 cm). (File
                        .JPG
                        max. 1 MB)
                    </li>
                    <li>Scan of passport. (File .JPG max. 1 MB)</li>
                    <li>A medical certificate. (Conditional) (File .PDF max. 1 MB)</li>
                    <li>Please fill out Declaration form in this link&nbsp;
                        <a href={'http://bit.ly/declarationletter'} target={'_blank'}
                           rel="noopener noreferrer">http://bit.ly/declarationletter</a>, sign, and scan it
                        in PDF file format. (max. 1 MB)
                    </li>
                </ul>
                <Typography variant="body1" gutterBottom>
                    You can save and revise those documents anytime before
                    clicking <strong>SUBMIT</strong> at the end of the &nbsp;
                    <strong>CONFIRMATION SECTION</strong>. Please be advised that the submission deadline is
                    on 1 May 2020 at
                    23.59 (GMT+7).
                </Typography>
            </Grid>
        </TabPanel>
        <TabPanel value={value} index={1}>
            <PersonalDetails userStore={userStore}/>
        </TabPanel>
        <TabPanel value={value} index={2}>
            <EducationBackground userStore={userStore}/>
        </TabPanel>
        <TabPanel value={value} index={3}>
            <HealthCondition userStore={userStore}/>
        </TabPanel>
        <TabPanel value={value} index={4}>
            <PersonalStatement userStore={userStore}/>
        </TabPanel>
        <TabPanel value={value} index={5}>
            <EmergencyContactDetail userStore={userStore}/>
        </TabPanel>
        <TabPanel value={value} index={6}>
            <ApplicantMedia userStore={userStore}/>
        </TabPanel>
        <TabPanel value={value} index={7}>
            <ApplicationFormConfirm userStore={userStore}/>
        </TabPanel>
    </>));
};

export default TabPanelSideNavApplicationForm;
